import React, { ReactElement, useCallback, useRef, RefObject, useState, useMemo, useContext, useEffect } from 'react';
import { TournamentSettings, TournamentStatus } from '../interfaces';
import CSV from 'papaparse';

import componentStyles from '../css/crosstable.module.css';
import styles from './css/export.module.css';
import tournStyles from '../css/tournament.module.css';

import Player from '../models/player';
import TabSection from '../components/TabSection';
import { FaHeading, FaExclamationCircle, FaEye, FaDownload, FaPlusCircle, FaMinusCircle } from 'react-icons/fa';
import countries from '../assets/countries.json';
import { CountryCode } from 'fide-chess/lib/interfaces';
import Alt from '../components/AltTitlebar';

interface ExportProps {
	players: Player[]
	leave: (() => void)
}

export default function ExportComponent(props: ExportProps): ReactElement {

	const csv = useMemo(() => {
		let arr = props.players.map(p => [p.firstName, p.lastName, p.rating]);
		return CSV.unparse(arr);
	}, [props.players]);

	return (
		<>	
			<div className={styles.stage}>
				<Alt title='Knockout Pairing View' leave={props.leave} />
				<div className='container'>
					<div className={styles.pasteContainer}>
						<div className={styles.paste}>
							<div className={[styles.pasteArea].join(' ')}>
								<textarea
									className={styles.textarea}
									name='textArea'
									value={csv}
									onClick={(e) => (e.target as HTMLTextAreaElement).select()}
								/>
							</div>
						</div>
					</div>
				</div>
				<div className='container' style={{display: 'none' }}>
					<div className={styles.pasteContainer}>
						<div className={styles.paste}>
							<div className={[styles.pasteArea].join(' ')}>
								<textarea
									className={[styles.output, 'scrollable'].join(' ')}
									name='textArea'
									placeholder='Raw data output'
								/> 
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}