import React, { ReactElement } from 'react';
import styles from './css/alt.module.css';
import { MdExitToApp } from 'react-icons/md';

export default function Alt({
	title,
	leave,
	buttons
}: {
	title: string,
	leave: () => void
	buttons?: {
		name: string
		content: ReactElement
		show?: boolean
		color: string
		onClick?: () => void
	}[]
}) {

	return (
		<div className={styles.titles}>
			<div>{title}</div>
			<div className={['button', styles.button].join(' ')} onClick={leave}>
				<MdExitToApp style={{color: 'indianred'}} /> Back
			</div>
			{buttons?.map((b, i) => {
				return (
					<div key={['button', i].join('.')} className={['button', styles.button].join(' ')} onClick={b.onClick}>
						{React.cloneElement(b.content, { ...b.content.props, style: {
							...b.content.props.style,
							color: b.color
						}})}
						{b.name}
					</div>
				);
			})}
		</div>
	);
}